import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()

export class IpService {
    constructor(private http: HttpClient) {
    }

    getIp() {
        return this.http.get("https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?appid=d55be7d670bc38520d2dfbccaf6528b9&q=HaNoi")
        .toPromise()
        .then()
    }
}