import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class WeatherService {

  constructor(private http: HttpClient) { }

  getTemp(cityName: string) {
    return this.http.get("https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?appid=d55be7d670bc38520d2dfbccaf6528b9&units=metric&q=" + cityName)
    .toPromise()
    .then()
  } 
}
